import config from '../config'
import helpers from '../helpers'
import ColorScheme from 'color-scheme'

import ChartZone from './ChartZone'
import moment from 'moment'

class Renderer {
    // top of hierarchy

    constructor(props) {
        // console.log(this.props.tyyyholt.asdf)

        this.ctx = props.ctx
        this.canvas = this.ctx.canvas
        this.animation = true

        this.retinaScale()

        this.w = this.canvas.width
        this.h = this.canvas.height

        this.config = this.scaleConfig(config)

        console.log(this.config)

        // const schemeTypes = ['mono', 'contrast', 'triade', 'tetrade', 'analogic']
        const schemeTypes = ['mono']

        const scheme = new ColorScheme;
        // const hue = Math.random() * 255;
        const hue = 235;
        console.log('current hue ð: ', hue)
        scheme.from_hue(hue)
            .scheme(schemeTypes[Math.floor(Math.random() * schemeTypes.length)])
            .variation('pastel');

        this.colors = scheme.colors();

        const totalUsedHeight = 1200
        const paddingVertical = (this.h - totalUsedHeight) / 2
        let currentHeightPointer = paddingVertical

        var f = new FontFace('Raleway', 'url(https://fonts.googleapis.com/css?family=Raleway)');
        this.children = [];
        const sequence = [
            () => {
                this.children.push(
                    new ChartZone({
                        chart: this,
                        city: 'Los Angeles',
                        bounds: {
                            xstart: 100 - 2 * 3 * 60,
                            xend: this.w,
                            ystart: currentHeightPointer,
                            yend: currentHeightPointer + 400
                        },
                        date: moment().subtract(1, 'day'),
                        elements: [
                            {
                                "start":-360 + 600,
                                "end": 720 - 360,
                                "text": "Stuff",
                                "color": "blue"
                            },
                            {
                                "start": -360 + 780,
                                "end": 960 - 360,
                                "text": "Emails",
                                "color": "blue"
                            },
                            {
                                "start":-360 + 1020,
                                "end": 1080 - 360,
                                "text": "Founder talk",
                                "color": "blue"
                            },
                            {
                                "start":-360 + 1140,
                                "end": 1320 - 360,
                                "text": "OKRs",
                                "color": "blue"
                            }
                        ]
                    })
                )

                setTimeout(() => {

                    this.children.push(
                        new ChartZone({
                            chart: this,
                            city: 'Los Angeles',
                            date: moment(),
                            bounds: {
                                xstart: 100 + 2 * (9 * 60),
                                xend: this.w,
                                ystart: currentHeightPointer,
                                yend: currentHeightPointer + 400
                            },
                            elements: [
                                {
                                    "start": -360 + 360,
                                    "end": 480 - 360,
                                    "text": "Morning coffe âï¸",
                                    "color": "blue"
                                },
                                {
                                    "start": -360 + 565,
                                    "end": 720 - 360,
                                    "text": "Emails",
                                    "color": "blue",
                                },
                                {
                                    "start": -360 + 780,
                                    "end": 840 - 360,
                                    "text": "Meet",
                                    "color": "blue",
                                },
                                {
                                    "start": -360 + 900,
                                    "end": 960 - 360,
                                    "text": "Meet people",
                                    "color": "blue"
                                },
                                {
                                    "start": -240 + 1160,
                                    "end": 1500 - 240,
                                    "text": "Go out drinking ð¥",
                                    "color": "blue",
                                }
                            ]
                        })
                    )

                }, 2000)
            },
            () => {
                this.children.forEach(c => c.changeShape())
            }
        ]

        let cur = 0
        window.addEventListener('keydown', (event) => {
            const key = event.key; // "ArrowRight", "ArrowLeft", "ArrowUp", or "ArrowDown"
            if (key == 'ArrowRight') {
                console.log('yes')
                if (cur >= sequence.length) {
                    console.log('no')
                    return
                }
                sequence[cur]()
                cur += 1

            }
        });

        this.children.push(
            new ChartZone({
                chart: this,
                city: 'Oslo',
                bounds: {
                    xstart: 100 + 10 + 9 * 60,
                    xend: this.w,
                    ystart: currentHeightPointer + 550,
                    yend: currentHeightPointer + 550 + 400
                },
                date: moment(),
                elements: [
                    {
                        "start": -360 + 400,
                        "end": 540 - 340,
                        "text": "Morning routine âï¸",
                        "color": "blue",
                    },
                    {
                        "start": -360 + 600,
                        "end": 690 - 360,
                        "text": "Meeting",
                        "color": "blue"
                    },
                    {
                        "start": -360 + 750,
                        "end": 780 - 360,
                        "text": "Lunch",
                        "color": "red"
                    },
                    {
                        "start": -360 + 900,
                        "end": 1020 - 360,
                        "text": "Something",
                        "color": "blue"
                    },
                    {
                        "start": -360 + 1050,
                        "end": 1140 - 360,
                        "text": "Something",
                        "color": "blue"
                    },
                    {
                        "start": -360 + 1290,
                        "end": 1350 - 360,
                        "text": "Evening ritual ð",
                        "color": "blue",
                    }
                ]
            })
        )

        


        this.render()
    }

    render = () => {
        const { ctx } = this
        ctx.clearRect(0, 0, this.w, this.h)
        ctx.textAlign = 'center'
        ctx.textBaseline = 'middle'

        this.children.forEach(child => {
            // ctx.save()
            // ctx.translate(child.bounds.x, child.bounds.y)
            child.render()
            // ctx.restore()
        })
        if (this.animation) {
            requestAnimationFrame(this.render)
        }
    }

    retinaScale = () => {
        // if (typeof window === 'undefined') {
        //     // we are in node.js
        //     return
        // }
        const { canvas } = this

        const dpr = window.devicePixelRatio
        const _parent = canvas.parentNode

        canvas.width = dpr * _parent.offsetWidth
        canvas.height = dpr * _parent.offsetHeight

        canvas.style.width = '100%'
        canvas.style.height = '100%'
    }

    scaleConfig = config => {
        // var scaledConfig = helpers.clone(config)
        const ratio = Math.min(this.w / 90, this.h / 90)
        function scaleFn(base, value, key) {
            if (value > 1 || value < 1 || value === 1) { // if value is a number
                base[key] = value * ratio
            }
        }
        helpers.deepEach(config, scaleFn)
        return config
    }
}

export {
    Renderer
}


